"use client"
import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, MessageSquare } from 'lucide-react';
import { ChatCompletionMessage } from './chatInterface';

export default function ChatPopup() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleChat = () => {
    setIsOpen(!isOpen);
  };

  const [messages, setMessages]= useState<ChatCompletionMessage[]>([]);

  const [message, setMessage] = useState("");
  const handleMessage =async()=>{};

  return (
    <div>
      {/* Floating Button */}
      <button
        onClick={toggleChat}
        className="fixed bottom-6 right-6 p-4 bg-blue-600 text-white rounded-full shadow-lg hover:bg-blue-700 focus:outline-none"
      >
        {isOpen ? <X size={24} /> : <MessageSquare size={24} />}
      </button>

      {/* Chatbox Popup */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: 50 }}
            className="fixed bottom-20 right-6 w-80 bg-white rounded-lg shadow-2xl flex flex-col overflow-hidden"
          >
            {/* Header */}
            <div className="bg-blue-600 text-white px-4 py-2 font-semibold">
              Let Talk ðŸ’¬
            </div>

            {/* Chat Messages */}
            <div className="flex-1 p-4 overflow-y-auto text-sm space-y-2">
              {/* Example Messages */}
              <div className="bg-gray-100 p-2 rounded-md self-start max-w-[75%]">
                Hi there! How can we help you?
              </div>
            </div>

            {/* Input */}
            <div className="border-t p-2">
              <input
                type="text"
                placeholder="Type a message..."
                value={message}
                onChange={(event)=>setMessage(event.target.value)}
                onKeyDown={async(event)=>{
                  if(event.key === 'Enter'){
                    await handleMessage();
                  }
                }}
                className="w-full border rounded-full px-4 py-2 text-sm focus:outline-none"
              />
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
